import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { DATES, InputGroupInline, InputType, WindowComponent, WindowComponentProps } from 'summer';
import { hasEmpresa } from '../../../../../dependencies/empresaDependency';
import { hasEmpresaMLF } from '../../../../../dependencies/empresaMLFDependency';
import { getDefaultReportBasicValues } from '../../../../../models/dtos/ger/reportBasicValues';
import { getDefaultReportViewOptions, ReportViewOptions } from '../../../../../models/dtos/ger/reportViewOptions';
import { CODIGO_PRODUTO, ORDENACAO_SAIDA_PRODUTO_ENUM_VALUES } from '../../../../../models/enumerated/mlf/ordenacaoSaidaProdutoEnum';
import { ORIGEM_LANCAMENTO_MLF_ENUM_VALUES } from '../../../../../models/enumerated/mlf/origemLancamentoMLFEnum';
import { ANALITICO, SINTETICO_ANALITICO_ENUM_VALUES } from '../../../../../models/enumerated/mlf/sinteticoAnaliticoEnum';
import { Reducers } from '../../../../../store/ducks';
import { GlobalState } from '../../../../../store/ducks/global.duck';
import ReportForm from '../../../../components/report/ReportForm';
import { CLASS_KEY, REPORT_EVENT, REPORT_TITLE, SaidaProdutoRV } from './saidaProdutoRV';

const DEFAULT_VALUES: SaidaProdutoRV = {
  ...getDefaultReportBasicValues(REPORT_TITLE),
  gerarCabecalhoPadraoAdministradora: true,
  sinteticoAnalitico: ANALITICO.value,
  tipoOrdenacao: CODIGO_PRODUTO.value,
};

const DEFAULT_OPTIONS: ReportViewOptions = {
  ...getDefaultReportViewOptions(REPORT_EVENT, DATES.value),
  exibirConfiguracaoDadosEmpresa: false,
};

const SaidaProdutoReportView: FC<WindowComponentProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const { handleSubmit, ...formProps } = useForm<SaidaProdutoRV>({ defaultValues: DEFAULT_VALUES, mode: 'onBlur' });

  return (
    <WindowComponent
      title="Relatório Saídas por Produto"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMLF(globalReducer.globalParameter)]}
    >
      <ReportForm
        {...props}
        handleSubmit={handleSubmit}
        formProps={formProps}
        localStorageKey={CLASS_KEY}
        routeProps={props}
        reportBasicValues={DEFAULT_VALUES}
        reportViewOptions={DEFAULT_OPTIONS}
      >
        <Row>
          <Col>
            <InputGroupInline
              id="sinteticoAnalitico"
              label="Modelo de Relatório"
              isPrependLabel={true}
              placeholder="Informe o Modelo de Relatório"
              formProps={{ ...formProps, validation: { required: true } }}
              inputDropDownProps={{
                isClearable: false,
                optionsProvider: SINTETICO_ANALITICO_ENUM_VALUES,
              }}
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="tipoOrdenacao"
              label="Tipo de Ordenação"
              isPrependLabel={true}
              placeholder="Informe o Tipo de Ordenação"
              formProps={{ ...formProps, validation: { required: true } }}
              inputDropDownProps={{
                isClearable: false,
                optionsProvider: ORDENACAO_SAIDA_PRODUTO_ENUM_VALUES,
              }}
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="origemLancamentoInt"
              label="Origem do Lançamento"
              isPrependLabel={true}
              placeholder="Informe a Origem do Lançamento"
              formProps={formProps}
              inputDropDownProps={{
                optionsProvider: ORIGEM_LANCAMENTO_MLF_ENUM_VALUES,
              }}
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
      </ReportForm>
    </WindowComponent>
  );
};

export default SaidaProdutoReportView;
